
#goods{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 174px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;
}
#goods .menu-wrapper{
  -webkit-box-flex: 0;
      -ms-flex: 0 0 80px;
          flex: 0 0 80px;
  width: 80px;
  background: #f3f5f7;
}
.menu-wrapper .menu-item{
  display: table;
  height: 54px;
  width: 56px;
  line-height: 14px;
  padding: 0 12px;
}
.menu-item .icon{
  display: inline-block;
  width: 12px;
  height: 12px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.menu-item .text{
  display: table-cell;
  width: 56px;
  vertical-align: middle;
  font-size: 12px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.text .decrease{
  background-image: url("decrease_3@2x.png");
}
.text .discount{
  background-image: url("discount_3@2x.png");
}
.text .special{
  background-image: url("special_3@2x.png");
}
.text .invoice{
  background-image: url("invoice_3@2x.png");
}
.text .guarantee{
  background-image: url("guarantee_3@2x.png");
}
#goods .foods-wrapper{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.foods-wrapper .title{
  padding-left: 14px;
  height: 26px;
  line-height: 26px;
  border-left: 2px solid #d9dde1;
  font-size: 12px;
  color: rgb(147,153,159);
  background: #f3f5f7;
}
.foods-wrapper .food-item{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 18px;
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.food-item:last-child{
  border-bottom: 0;
}
.food-item .icon{
  -webkit-box-flex: 0;
      -ms-flex: 0 0 57px;
          flex: 0 0 57px;
  margin-right: 10px;
}
.food-item .content{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.content .name{
  font-size: 14px;
  margin: 2px 0 8px 0;
  height: 14px;
  line-height: 14px;
  color: rgb(7,17,17);
}
.content .desc,.extra{
  line-height: 10px;
  font-size: 10px;
  color: rgb(147,153,159);
}
.content .desc{
  margin-bottom: 8px;
}
.content .extra .count{
  margin-right: 12px;
}
.content .price{
  font-size: 700;
  line-height: 24px;
}
.content .price .now{
  margin=right: 8px;
  font-size: 14px;
  color: rgb(240, 20, 20);
}
.content .price .old{
  text-decoration: line-through;
  font-size: 10px;
  color: rgb(147,153,159);
}
@media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3){
.text .decrease{
  background-image: url("decrease_3@3x.png");
}
.text .discount{
  background-image: url("discount_3@3x.png");
}
.text .special{
  background-image: url("special_3@3x.png");
}
.text .invoice{
  background-image: url("invoice_3@3x.png");
}
.text .guarantee{
  background-image: url("guarantee_3@3x.png");
}
}



